.header {
  width: 100%;
  padding: 10px 30px;
  background: #F0F0F0; }
  .header__title {
    padding-left: 54px; }
  .header__logo {
    width: 100%;
    max-width: 290px; }
  .header__btn {
    margin-right: 20px;
    background: #9D3C3C;
    color: #FFF;
    transition: all .5s ease; }
  .header__btn:hover {
    color: #FFF;
    background: #E83C3C; }
  .header__avatar {
    width: 44px;
    height: 44px; }
  .header__menu {
    width: 22px;
    height: 22px;
    background: url(./static/hamburger-icon.svg) no-repeat center center; }

@media screen and (max-width: 768px) {
  .header {
    position: relative;
    z-index: 100;
    padding: 8px 16px; }
    .header__menu {
      margin-left: 16px; }
    .header__avatar {
      width: 28px;
      height: 28px; } }
