.sidebar {
  width: 100%;
  max-width: 340px;
  transition: max-width .5s ease;
  background: #E2E2E2;
  position: relative;
  flex-grow: 1; }
  .sidebar__logo {
    max-height: 64px;
    padding: 10px 48px;
    background: #F0F0F0; }

.sidebar[data-folded="true"] {
  max-width: 75px; }
  .sidebar[data-folded="true"] .nav {
    position: static; }
    .sidebar[data-folded="true"] .nav__list {
      padding: 40px 25px; }
    .sidebar[data-folded="true"] .nav__link-txt {
      display: none; }
    .sidebar[data-folded="true"] .nav__collapse-btn {
      position: absolute;
      bottom: 20px; }
    .sidebar[data-folded="true"] .nav__collapse-svg {
      transform: rotate(180deg); }
    .sidebar[data-folded="true"] .nav__collapse-txt {
      display: none; }
    .sidebar[data-folded="true"] .nav .instructions {
      padding-right: 0;
      background: none; }
    .sidebar[data-folded="true"] .nav__expand-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 50px;
      margin-bottom: 100px;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background-color: #9D3C3C; }
    .sidebar[data-folded="true"] .nav__expand-btn:hover {
      background-color: #E83C3C; }

.nav {
  font-size: 14px;
  font-weight: 600;
  position: relative;
  color: #4E4E4E; }
  .nav__list {
    padding: 40px 30px;
    width: 100%; }
  .nav__list--bordered {
    border-bottom: 1px solid #D0D0D0; }
  .nav__list-item {
    display: flex;
    align-items: center; }
  .nav__list-item:not(:first-child) {
    margin-top: 36px; }
  .nav__list-link {
    display: inline-block;
    background-repeat: no-repeat;
    background-position: left center;
    background-size: contain; }
  .nav__list-link:hover,
  .nav__list-link:hover .nav__list-svg {
    color: #E83C3C;
    fill: #E83C3C; }
  .nav__link-txt {
    padding-left: 22px; }
  .nav__list-link--active,
  .nav__list-link--active .nav__list-svg {
    color: #E83C3C;
    fill: #E83C3C; }
  .nav .instructions {
    background-image: url(./static/arrow-down-icon.svg);
    background-size: auto;
    background-position: right center;
    padding-right: 48px; }
  .nav__collapse-btn {
    display: flex;
    align-items: center;
    margin: 0 auto;
    margin-bottom: 40px;
    font-size: 16px;
    font-weight: 700; }
  .nav__collapse-txt {
    padding-left: 16px; }
  .nav__expand-btn {
    display: none; }

@media screen and (max-width: 768px) {
  .sidebar-hidden {
    height: 0;
    opacity: 0; }
  .sidebar-visible {
    transition: opacity 1s ease-out;
    height: auto;
    opacity: 1; }
  .sidebar {
    position: absolute;
    right: 0;
    top: 59px;
    z-index: 99; }
  .nav__btn {
    margin-top: 20px;
    background-color: #9D3C3C;
    color: #FFF;
    margin-bottom: 25px;
    transition: all .5s ease; }
  .nav__btn:hover {
    background: #E83C3C; } }

@media screen and (max-width: 576px) {
  .sidebar {
    position: absolute;
    z-index: 99;
    top: 44px;
    right: 0;
    width: auto; }
  .nav {
    font-size: 12px; }
    .nav__list {
      padding: 25px 17px; }
    .nav__list-item:not(:first-child) {
      margin-top: 18px; } }

/**************END SIDEBAR******************/
