.footer {
  padding: 16px;
  background: #F2F2F2;
  font-size: 12px;
  color: #919197; }
  .footer__list {
    display: flex;
    flex-wrap: wrap;
    margin-top: 8px;
    white-space: nowrap; }
  .footer__list-link {
    border-bottom: 1px solid #727272; }
  .footer__list-link:hover {
    color: #E83C3C;
    border-bottom: 1px solid #E83C3C; }
  .footer__list-item {
    margin-top: 8px; }
  .footer__list-item:not(:last-child) {
    margin-right: 24px; }
  .footer__license {
    margin-top: 16px; }

.social-list__item:not(:first-child) {
  margin-left: 12px; }

.social-list__link {
  display: inline-block;
  width: 34px;
  height: 34px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center; }

.social-list .vk {
  background-image: url(./static/vk-icon.svg); }

.social-list .facebook {
  background-image: url(./static/facebook-icon.svg); }

.social-list .youtube {
  background-image: url(./static/youtube-icon.svg); }

@media screen and (max-width: 576px) {
  .footer {
    font-size: 8px; }
  .social-list__link {
    width: 25px;
    height: 25px; } }
