.info__list {
  font-weight: 500; }

.info__list-item--bordered {
  padding-bottom: 40px;
  margin-bottom: 40px;
  border-bottom: 1px solid #EAEAEA; }

.info__list-item:not(:first-child) {
  margin-bottom: 32px; }

.info__list-descr {
  color: #919197; }

.info__list-value--bordered {
  border: 1px solid #EAEAEA;
  padding: 6px 12px;
  min-width: 240px;
  display: inline-block; }

.info__list-link {
  margin-left: 6px; }

.panel__avatar-link {
  margin-top: 12px;
  white-space: nowrap; }

.panel__avatar-img {
  max-width: 170px; }

@media screen and (max-width: 1200px) {
  .info {
    margin-top: 50px; } }

@media screen and (max-width: 992px) {
  .info__list {
    font-size: 14px; }
  .info__list-item--bordered {
    padding-bottom: 20px;
    margin-bottom: 20px; }
  .panel__avatar-img {
    width: 120px; } }

@media screen and (max-width: 768px) {
  .mobile {
    display: block; }
  .pc {
    display: none; }
  .panel__title {
    margin-bottom: 20px; } }

@media screen and (max-width: 576px) {
  .info__list {
    font-size: 12px; }
  .edit-profile-btn {
    font-size: 10px; }
  .panel__avatar-img {
    width: 80px; } }
