.ml-auto {
  margin-left: auto; }

.no-gutters {
  margin: 0 !important;
  padding: 0 !important; }

.no-gutters > [class*="col"] {
  padding-right: 0 !important;
  padding-left: 0 !important; }

.container, .container-fluid {
  padding: 0; }

* {
  box-sizing: border-box; }

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li,
a {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-decoration: none;
  color: inherit; }

button {
  border: none;
  background: transparent;
  padding: 0;
  margin: 0;
  color: inherit; }

body {
  margin: 0;
  min-height: 100vh;
  line-height: 1.22;
  font-family: 'Montserrat', serif;
  position: relative;
  display: flex;
  flex-direction: column; }

.main {
  padding: 54px;
  flex-grow: 1; }

.content {
  flex-grow: 1; }

.title {
  font-size: 24px;
  font-weight: 700;
  color: #4E4E4E; }

.btn {
  color: #FFF;
  background: #E83C3C;
  transition: all .5s ease; }

.btn:hover {
  color: #FFF;
  background: #9D3C3C; }

.btn-sm {
  padding: 8px 36px;
  border-radius: 32px; }

.btn-md {
  padding: 16px 49px;
  border-radius: 35px; }

.max-768-visible {
  display: none; }

.min-768-visible {
  display: block; }

.mobile {
  display: none; }

.pc {
  display: block; }

.hidden {
  display: none; }

.be-static {
  position: static; }

.add-obj-btn {
  background: #E83C3C;
  color: #FFF;
  font-size: 24px;
  font-weight: 700;
  width: 195px;
  height: 195px;
  border-radius: 50%;
  display: inline-block;
  transition: all .5s ease; }

.add-obj-btn:hover {
  background: #9D3C3C; }

.link {
  color: #E83C3C;
  font-size: 12px;
  font-weight: 500;
  transition: all .5s ease; }

.link:hover {
  color: #9D3C3C; }

@media screen and (max-width: 1200px) {
  .main {
    padding: 16px; } }

@media screen and (max-width: 992px) {
  .add-obj-btn {
    width: 120px;
    height: 120px;
    font-size: 12px; } }

@media screen and (max-width: 768px) {
  .min-768-visible {
    display: none; }
  .max-768-visible {
    display: block; }
  .opacity {
    background: rgba(78, 78, 78, 0.8);
    position: absolute;
    z-index: 98;
    width: 100vw;
    height: 100%; } }

@media screen and (max-width: 576px) {
  .add-obj-btn {
    width: 80px;
    height: 80px;
    font-size: 12px; }
  .link {
    font-size: 10px; }
  .title {
    font-size: 18px; } }
